import 'lazysizes/plugins/object-fit/ls.object-fit'
import 'lazysizes/plugins/parent-fit/ls.parent-fit'
import 'lazysizes/plugins/rias/ls.rias'
import 'lazysizes/plugins/bgset/ls.bgset'
import 'lazysizes'
import 'lazysizes/plugins/respimg/ls.respimg'

import '../../styles/theme.scss'

import $ from 'jquery'
import {pageLinkFocus} from '@shopify/theme-a11y'
import {cookiesEnabled} from '@shopify/theme-cart'
import {wrapTable, wrapIframe} from '@shopify/theme-rte'

import customizeSelect from './customize-select'
import cart from './cart'
import productTemplate from '../templates/product'
import initializeLogo, {initializeDesktopLogo, initializeMobileLogo} from './logoAnimation'


window.slate = window.slate || {}
window.theme = window.theme || {}

$(document).ready(() => {
  $('body').css('width', '+=1');

  //Remove width css
  setTimeout(function () {
    $('body').css('width', '');
  }, 1);

  //init logo
  const path = location.pathname
  const windowWidth = $(window).width()

  if (windowWidth > 767) {
    if (path === '/' || (path.indexOf('/collections/') + 1 && path.split('/').length === 3)) {
      initializeLogo()
    } else {
      setStylesForHeaderItems()
    }
  } else {
    if (path === '/') {
      initializeLogo()
    } else {
      setStylesForMobileLogo()
    }
  }

  // Common a11y fixes
  if (window.location.hash !== '#') {
    pageLinkFocus($(window.location.hash))
  }

  $('.in-page-link').on('click', (evt) => {
    pageLinkFocus($(evt.currentTarget.hash))
  })

  // Target tables to make them scrollable
  const tableSelectors = '.rte table'

  wrapTable({
    $tables: $(tableSelectors),
    tableWrapperClass: 'rte__table-wrapper',
  })

  // Target iframes to make them responsive
  const iframeSelectors =
    '.rte iframe[src*="youtube.com/embed"],' +
    '.rte iframe[src*="player.vimeo"]'

  wrapIframe({
    $iframes: $(iframeSelectors),
    iframeWrapperClass: 'rte__video-wrapper',
  })

  // Apply a specific class to the html element for browser support of cookies.
  if (cookiesEnabled()) {
    document.documentElement.className = document.documentElement.className.replace(
      'supports-no-cookies',
      'supports-cookies',
    )
  }

  // Customize select
  customizeSelect(document.getElementById('header-menu'))

  // Product template initializations
  if (document.body.classList.contains('template-product')) {
    productTemplate.init()
  }
})

function setStylesForHeaderItems() {
  const cart = $('.header .cart')
  const search = $('.header .search')
  const select = $('.header .select-wrapper')

  cart.css({
    'border-color': `rgba(0, 0, 0, 1)`,
    'background-color': '#ffffff',
  })
  search.css({
    'border-color': `rgba(0, 0, 0, 1)`,
    'background-color': '#ffffff',
  })
  select.css({
    'border-color': `rgba(0, 0, 0, 1)`,
    'background-color': '#ffffff',
  })
}

function setStylesForMobileLogo() {
  const allLogoWords = $('.logo-bg .logo-word')
  const firstSmallPinWordEl = $('.logo-bg .pin.first')
  const secondSmallPinWordEl = $('.logo-bg .pin.second')
  const itWordEl = $('.logo-bg .it')

  firstSmallPinWordEl.css({
    top: '5px',
    right: '5px',
  })
  secondSmallPinWordEl.css({
    left: '5px',
    bottom: '5px',
  })
  itWordEl.css({
    right: '5px',
    bottom: '5px',
  })

  allLogoWords.on('click', () => location.pathname = '/')
}