export default (select) => {
	const options = select.getElementsByTagName('option');
	const selectedOption = select.querySelector('option[selected]');

	select.classList.add('select-hidden');
	const wrapper = document.createElement('div');
	wrapper.classList.add('select');
	select.parentNode.insertBefore(wrapper, select);
	wrapper.appendChild(select);
	const selectStyled = document.createElement('div');
	selectStyled.classList.add('select-styled');
	wrapper.appendChild(selectStyled);
	selectStyled.innerHTML = selectedOption ? selectedOption.label : options[0].label;
	const list = document.createElement('ul');
	list.className = 'select-options';
	wrapper.appendChild(list);
	for (let i = 0; i < options.length; i++) {
		const li = document.createElement('li');
		li.innerHTML = options[i].label;
		li.setAttribute('rel', options[i].value);
		li.onclick = (event) => {
			event.stopPropagation();
			window.location.href = options[i].value;
		};
		list.appendChild(li);
	}

	selectStyled.onclick = (event) => {
		event.stopPropagation();
		const element = event.currentTarget;
		element.classList.toggle('active');
		const optionsList = element.nextElementSibling;
		optionsList.classList.toggle('--active');
	};

	document.addEventListener('click', () => {
		selectStyled.classList.remove('active');
		selectStyled.nextElementSibling.classList.remove('--active');
	});
};