import $ from "jquery";

const config = {
  bigPinWordWidthPersent: 26.5745,
  smallPinWordWidth: 26.5740,
  itWordWidth: 16.7839,
  scrollRange: {
    min: 0,
    max: null,
  },
  logoWords: [],
  firstSmallPinMaxTranslate: null,
  secondSmallPinMaxTranslate: null,
  mobileLogoWords: [],
}

const elements = {
  allLogoWords: null,
  headerLogoInner: null,
  bigPinWordEl: null,
  firstSmallPinWordEl: null,
  secondSmallPinWordEl: null,
  itWordEl: null,
  cart: null,
  search: null,
  select: null,
}

function initializeElements() {
  elements.allLogoWords = $('.logo-bg .logo-word')
  elements.allAnimatedElements = $('.logo-bg .logo-word, .logo-bg .cart, .logo-bg .search, .logo-bg .select-wrapper')
  elements.headerLogoInner = $('.logo-bg')
  elements.bigPinWordEl = $('.logo-bg .pin-big')
  elements.firstSmallPinWordEl = $('.logo-bg .pin.first')
  elements.secondSmallPinWordEl = $('.logo-bg .pin.second')
  elements.itWordEl = $('.logo-bg .it')
  elements.cart = $('.header .cart')
  elements.search = $('.header .search')
  elements.select = $('.header .select-wrapper')
}

function setHeaderElementsBorderOpacity(opacity) {
  elements.cart.css('border-color', `rgba(0, 0, 0, ${opacity})`)
  elements.search.css('border-color', `rgba(0, 0, 0, ${opacity})`)
  elements.select.css('border-color', `rgba(0, 0, 0, ${opacity})`)
}

function setHeaderElementsBackground(transparent) {
  if (transparent) {
    elements.cart.css('background-color', 'transparent')
    elements.search.css('background-color', 'transparent')
    elements.select.css('background-color', 'transparent')
  } else {
    elements.cart.css('background-color', '#ffffff')
    elements.search.css('background-color', '#ffffff')
    elements.select.css('background-color', '#ffffff')
  }
}

function willCangeClassToggle(bool) {
  const windowWidth = $(window).width()

  if (windowWidth > 768) {
    elements.allAnimatedElements.each((index, el) => {
      if (bool) {
        if (!$(el).hasClass('will-change')) {
          $(el).addClass('will-change')
        }
      } else {
        $(el).removeClass('will-change')
      }
    })
  } else {
    elements.allLogoWords.each((index, el) => {
      if (bool) {
        if (!$(el).hasClass('will-change')) {
          $(el).addClass('will-change')
        }
      } else {
        $(el).removeClass('will-change')
      }
    })
  }
}

function setLogoStyles(scale, firsrSmallPinTranslate, secondSmallPinTranslate) {
  if (config.logoWords.length) {
    for (let i = 0; i < scale.length; i++) {
      if (scale[i] < config.logoWords[i].min) {
        scale[i] = config.logoWords[i].min
      } else if (scale[i] > 1) {
        scale[i] = 1
      }
    }
  }

  if (firsrSmallPinTranslate >= 0) {
    firsrSmallPinTranslate = 0
  } else if (firsrSmallPinTranslate < config.logoWords[1].maxTranslateX) {
    firsrSmallPinTranslate = config.logoWords[1].maxTranslateX
  } else if (!firsrSmallPinTranslate) {
    firsrSmallPinTranslate = config.logoWords[1].maxTranslateX
  }

  if (secondSmallPinTranslate >= 0) {
    secondSmallPinTranslate = 0
  } else if (secondSmallPinTranslate < config.logoWords[2].maxTranslateX) {
    secondSmallPinTranslate = config.logoWords[2].maxTranslateX
  } else if (!secondSmallPinTranslate) {
    secondSmallPinTranslate = config.logoWords[2].maxTranslateX
  }

  elements.bigPinWordEl.css({
    transform: `scale(${scale[0]})`,
  })
  elements.firstSmallPinWordEl.css({
    transform: `scale(${scale[1]}) translateX(${firsrSmallPinTranslate}px)`,
  })
  elements.secondSmallPinWordEl.css({
    transform: `scale(${scale[2]}) translateX(${secondSmallPinTranslate}px)`,
  })
  elements.itWordEl.css({
    transform: `scale(${scale[3]})`,
  })
}

function logoStylesReset() {
  elements.bigPinWordEl.attr('style', '')
  elements.firstSmallPinWordEl.attr('style', '')
  elements.secondSmallPinWordEl.attr('style', '')
  elements.itWordEl.attr('style', '')
}

function getTranslate(smallPinWordWidth, smallPinWordMinWidth) {
  const selectOffsetLeftFromRightSide = elements.select.offset().left + elements.select.innerWidth()
  const pxBetweenCartAndSelect = elements.cart.offset().left - selectOffsetLeftFromRightSide
  const cartOffsetLeftFromRightSide = elements.cart.offset().left + elements.cart.innerWidth()
  const pxBetweenCartAndSearch = elements.search.offset().left - cartOffsetLeftFromRightSide
  const pinWidth = elements.firstSmallPinWordEl.innerWidth()
  const cooficient = smallPinWordWidth / smallPinWordMinWidth

  const val1 = pxBetweenCartAndSelect / 2 - pinWidth / 2
  const val2 = pxBetweenCartAndSearch / 2 - pinWidth / 2

  const x1 = -((elements.firstSmallPinWordEl.offset().left - val1 - selectOffsetLeftFromRightSide) * (cooficient))
  const x2 = -((elements.secondSmallPinWordEl.offset().left - val2 - cartOffsetLeftFromRightSide) * (cooficient))

  return [x1, x2]
}

function initializeLogoSize() {
  const headerLogoWidthPersent = elements.headerLogoInner.width() / 100
  const bigPinWordWidth = headerLogoWidthPersent * config.bigPinWordWidthPersent
  const smallPinWordWidth = headerLogoWidthPersent * config.smallPinWordWidth
  const itWordWidth = headerLogoWidthPersent * config.itWordWidth

  const bigPinWordMinHeight = elements.bigPinWordEl.height()
  const smallPinWordMinHeight = elements.firstSmallPinWordEl.height()
  const itWordWordMinHeight = elements.itWordEl.height()

  const bigPinWordMinWidth = elements.bigPinWordEl.width()
  const smallPinWordMinWidth = elements.firstSmallPinWordEl.width()
  const itWordWordMinWidth = elements.itWordEl.width()

  const bigPinWordCooficient = bigPinWordMinHeight / bigPinWordMinWidth
  const smallPinWordCooficient = smallPinWordMinHeight / smallPinWordMinWidth
  const itWordWordCooficient = itWordWordMinHeight / itWordWordMinWidth

  const bigPinWordScale = bigPinWordMinWidth / bigPinWordWidth
  const smallPinWordScale = smallPinWordMinWidth / smallPinWordWidth
  const itWordScale = itWordWordMinWidth / itWordWidth

  elements.bigPinWordEl.css({
    width: bigPinWordWidth,
    height: bigPinWordWidth * bigPinWordCooficient,
    minHeight: bigPinWordMinHeight,
  })
  elements.firstSmallPinWordEl.css({
    width: smallPinWordWidth,
    height: smallPinWordWidth * smallPinWordCooficient,
    minHeight: smallPinWordMinHeight,
  })
  elements.secondSmallPinWordEl.css({
    width: smallPinWordWidth,
    height: smallPinWordWidth * smallPinWordCooficient,
    minHeight: smallPinWordMinHeight,
  })
  elements.itWordEl.css({
    width: itWordWidth,
    height: itWordWidth * itWordWordCooficient,
    minHeight: itWordWordMinHeight,
  })

  const scale = [bigPinWordScale, smallPinWordScale, smallPinWordScale, itWordScale]

  const translates = getTranslate(smallPinWordWidth, smallPinWordMinWidth)

  const firstSmallPinTranslate = translates[0]
  const secondSmallPinTranslate = translates[1]

  config.logoWords = [
    {
      min: scale[0],
      max: 1,
      range: scale[0] - 1,
      maxTranslateX: 0,
    },
    {
      min: scale[1],
      max: 1,
      range: scale[1] - 1,
      maxTranslateX: firstSmallPinTranslate,
    },
    {
      min: scale[2],
      max: 1,
      range: scale[1] - 1,
      maxTranslateX: secondSmallPinTranslate,
    },
    {
      min: scale[3],
      max: 1,
      range: scale[1] - 1,
      maxTranslateX: 0,
    },
  ]

  setLogoStyles(scale, firstSmallPinTranslate, secondSmallPinTranslate)

  $('.logo-bg').css('height', 'auto')
  config.scrollRange.max = $('.logo-bg').outerHeight()

  $('.main-wrapper').css('padding-top', $('.logo-bg').height())
  if ($('.main-wrapper').innerHeight() < $('html').innerHeight() + $('.logo-bg').height()) {
    $('.main-wrapper').css('min-height', `calc(100vh + ${$('.logo-bg').height()}px)`)
  }
  $('header[role="banner"] .description').css('padding-top', '50px')
  $('.logo-bg').css('height', 0)
}

function getWordTranslateOfScale(scale, data) {
  const currentScalePersentOfRange = (scale - data.min) / data.range

  return data.maxTranslateX * currentScalePersentOfRange + data.maxTranslateX
}

const onScrollHandler = () => {
  let scrollTop = $(window).scrollTop()

  if (scrollTop < config.scrollRange.max) {
    if (scrollTop < 0) {
      scrollTop = 0
    }
    const scalePersent = 1 - scrollTop / config.scrollRange.max
    const borderOpacity = scrollTop / config.scrollRange.max

    const newScale = [
      config.logoWords[0].max * scalePersent,
      config.logoWords[1].max * scalePersent,
      config.logoWords[2].max * scalePersent,
      config.logoWords[3].max * scalePersent,
    ]

    const newFirstSmallPinTranslate = getWordTranslateOfScale(newScale[1], config.logoWords[1])
    const newSecondSmallPinTranslate = getWordTranslateOfScale(newScale[2], config.logoWords[2])

    if (scalePersent < 0.2) {
      setHeaderElementsBackground(false)
    } else {
      setHeaderElementsBackground(true)
    }
    setHeaderElementsBorderOpacity(borderOpacity)
    setLogoStyles(newScale, newFirstSmallPinTranslate, newSecondSmallPinTranslate)
    willCangeClassToggle(true)
  } else {
    setLogoStyles(
      [config.logoWords[0].min, config.logoWords[1].min, config.logoWords[2].min, config.logoWords[3].min],
      config.logoWords[1].maxTranslateX,
      config.logoWords[2].maxTranslateX
    )
    setHeaderElementsBorderOpacity(1)
    setHeaderElementsBackground(false)
  }
}

export const initializeDesktopLogo = () => {
  initializeElements()
  logoStylesReset()
  initializeLogoSize()
  onScrollHandler() // first styles for logo with scrollTop

  window.onscroll = onScrollHandler

  elements.allLogoWords.click(() => location.pathname = '/')
}

function initializeMobileLogoSize() {
  const windowHeight = $(window).height()
  const bigPinWordHeight = windowHeight * 0.22
  const smallPinWordHeight = windowHeight * 0.28
  const itWordHeight = windowHeight * 0.22

  const bigPinWordMinHeight = elements.bigPinWordEl.height()
  const smallPinWordMinHeight = elements.firstSmallPinWordEl.height()
  const itWordWordMinHeight = elements.itWordEl.height()

  const bigPinWordScale = bigPinWordMinHeight / bigPinWordHeight
  const smallPinWordScale = smallPinWordMinHeight / smallPinWordHeight
  const itWordScale = itWordWordMinHeight / itWordHeight

  elements.bigPinWordEl.css({
    height: bigPinWordHeight,
    minHeight: bigPinWordMinHeight,
  })
  elements.firstSmallPinWordEl.css({
    height: smallPinWordHeight,
    minHeight: smallPinWordMinHeight,
  })
  elements.secondSmallPinWordEl.css({
    height: smallPinWordHeight,
    minHeight: smallPinWordMinHeight,
  })
  elements.itWordEl.css({
    height: itWordHeight,
    minHeight: itWordWordMinHeight,
  })

  const scale = [bigPinWordScale, smallPinWordScale, smallPinWordScale, itWordScale]

  config.mobileLogoWords = [
    {
      min: scale[0],
      max: 1,
      range: scale[0] - 1,
      maxTranslate: {l: 5, t: 5},
    },
    {
      min: scale[1],
      max: 1,
      range: scale[1] - 1,
      maxTranslate: {
        r: parseInt(elements.firstSmallPinWordEl.css('right')),
        t: parseInt(elements.firstSmallPinWordEl.css('top')),
      },
    },
    {
      min: scale[2],
      max: 1,
      range: scale[1] - 1,
      maxTranslate: {
        l: parseInt(elements.secondSmallPinWordEl.css('left')),
        b: parseInt(elements.secondSmallPinWordEl.css('bottom')),
      },
    },
    {
      min: scale[3],
      max: 1,
      range: scale[1] - 1,
      maxTranslate: {l: 5, t: 5},
    },
  ]

  const translate = [
    {l: 5, t: 5},
    {r: config.mobileLogoWords[1].maxTranslate.r, t: config.mobileLogoWords[1].maxTranslate.t},
    {l: config.mobileLogoWords[2].maxTranslate.l, b: config.mobileLogoWords[2].maxTranslate.b},
    {l: 5, t: 5},
  ]

  setMobileLogoStyles(scale, translate)

  config.scrollRange.max = windowHeight

  $('.main-wrapper').css('padding-top', windowHeight)
}

function setMobileLogoStyles(scale, translate) {
  for (let i = 0; i < scale.length; i++) {
    if (scale[i] < config.mobileLogoWords[i].min) {
      scale[i] = config.mobileLogoWords[i].min
    }
  }

  if (translate[1].r > config.mobileLogoWords[1].maxTranslate.r) {
    translate[1].r = config.mobileLogoWords[1].maxTranslate.r
  } else if (translate[1].r < 5) {
    translate[1].r = 5
  }

  if (translate[1].t > config.mobileLogoWords[1].maxTranslate.t) {
    translate[1].t = config.mobileLogoWords[1].maxTranslate.t
  } else if (translate[1].t < 5) {
    translate[1].t = 5
  }

  if (translate[2].l > config.mobileLogoWords[2].maxTranslate.l) {
    translate[2].l = config.mobileLogoWords[2].maxTranslate.l
  } else if (translate[2].l < 5) {
    translate[2].l = 5
  }

  if (translate[2].b > config.mobileLogoWords[2].maxTranslate.b) {
    translate[2].b = config.mobileLogoWords[2].maxTranslate.b
  } else if (translate[2].b < 5) {
    translate[2].b = 5
  }

  elements.bigPinWordEl.css({
    transform: `scale(${scale[0]})`,
  })
  elements.firstSmallPinWordEl.css({
    transform: `scale(${scale[1]})`,
    right: translate[1].r,
    top: translate[1].t,
  })
  elements.secondSmallPinWordEl.css({
    transform: `scale(${scale[2]})`,
    left: translate[2].l,
    bottom: translate[2].b,
  })
  elements.itWordEl.css({
    transform: `scale(${scale[3]})`,
  })
}

function getMobileWordTranslateOfScale(scale, data, index) {
  const currentScalePersentOfRange = (scale - data.min) / data.range
  let r = 5, t = 5, l = 5, b = 5

  if (index === 1) {
    r = data.maxTranslate.r * currentScalePersentOfRange * (-1)
    t = data.maxTranslate.t * currentScalePersentOfRange * (-1)

    if (r < 5) {
      r = 5
    }
    if (t < 5) {
      t = 5
    }

    return {r, t}
  } else if (index === 2) {
    l = data.maxTranslate.l * currentScalePersentOfRange * (-1)
    b = data.maxTranslate.b * currentScalePersentOfRange * (-1)

    if (l < 5) {
      l = 5
    }
    if (b < 5) {
      b = 5
    }

    return {l, b}
  }
}

function onScrollMobileHandler() {
  let scrollTop = $(window).scrollTop()

  if (scrollTop < config.scrollRange.max) {
    if (scrollTop < 0) {
      scrollTop = 0
    }

    const scalePersent = 1 - scrollTop / config.scrollRange.max
    const newScale = [
      config.mobileLogoWords[0].max * scalePersent,
      config.mobileLogoWords[1].max * scalePersent,
      config.mobileLogoWords[2].max * scalePersent,
      config.mobileLogoWords[3].max * scalePersent,
    ]

    const translate = [
      {l: 5, t: 5},
      getMobileWordTranslateOfScale(newScale[1], config.mobileLogoWords[1], 1),
      getMobileWordTranslateOfScale(newScale[2], config.mobileLogoWords[2], 2),
      {l: 5, t: 5},
    ]

    willCangeClassToggle(true)
    setMobileLogoStyles(newScale, translate)
    $('.header .nav-item').css('z-index', 900)
    elements.allLogoWords.off('click')
  } else {
    setMobileLogoStyles(
      [config.mobileLogoWords[0].min, config.mobileLogoWords[1].min, config.mobileLogoWords[2].min, config.mobileLogoWords[3].min],
      [
        {l: 5, t: 5},
        {r: 5, t: 5},
        {l: 5, b: 5},
        {r: 5, b: 5},
      ]
    )
    $('.header .nav-item').css('z-index', 999)
    willCangeClassToggle(false)
    elements.allLogoWords.on('click', () => location.pathname = '/')
  }
}

export const initializeMobileLogo = () => {
  initializeElements()
  logoStylesReset()
  initializeMobileLogoSize()
  onScrollMobileHandler() // first styles for logo with scrollTop
  setHeaderElementsBorderOpacity(1)
  setHeaderElementsBackground(false)

  window.onscroll = onScrollMobileHandler
}

const initializeLogo = () => {
  const windowWidth = $(window).width()

  if (windowWidth > 767) {
    initializeDesktopLogo()
  } else {
    initializeMobileLogo()
  }

  window.onresize = () => {
    const windowWidth = $(window).width()

    if (windowWidth > 767) {
      initializeDesktopLogo()
    } else {
      initializeMobileLogo()
    }
  }

}

export default initializeLogo
